import axios from 'axios';
import MintUI from 'mint-ui';
import storage from "@/assets/js/util/storage.js";//localStorage sessionStorage 
import {ui} from "@/assets/js/util/page-ui.js";//
axios.defaults.timeout = 10000;
axios.defaults.retry = 1; //
axios.defaults.retryDelay = 1000; //
axios.defaults.shouldRetry = error => true; //
axios.defaults.baseURL = '/app_gw/';


axios.interceptors.request.use (
  config => {
   let isLoading = config.isLoading
   if (isLoading == undefined || isLoading) {
      MintUI.Indicator.open({//loading
        spinnerType: 'fading-circle'
      });
    }
    //token
    let loginUserAuthorization =storage.localStorage.getAuthorization();
    // let loginUserAuthorization =localStorage.getItem("authorization");
    if (!loginUserAuthorization) {
      loginUserAuthorization = '67899b8e2d464acf8fbe383b841bed9f';
    }
    config.headers.Authorization = loginUserAuthorization;
    return config;
  },
  err => {
     MintUI.Indicator.close();//loading
     ui.msg (err);
     return Promise.reject (err);
   }
   );

/*
 *200
 *1(message)
 *code message
 */
 axios.interceptors.response.use (
  response => {
     MintUI.Indicator.close();//loading
     if (response.data.code == 200) {
      return response.data;
    } else if (response.data.code == 2002) {
      ui.msg ('');
      window.location.replace ('/login/logout');
    } else {
      ui.msg (response.data.message);
      return response.data;
      //throw new Error(""+response.data.code+"\n \n "+response.data.message+"")
    }
  },
  error => {
     MintUI.Indicator.close();//loading
     if (error.code === 'ECONNABORTED' && error.message.indexOf ('timeout') !== -1) {
      ui.msg ('');
    }
    let config = error.config;
    // 
    if (!config || !config.retry) return Promise.reject (error);

    if (!config.shouldRetry || typeof config.shouldRetry != 'function') {
      return Promise.reject (error);
    }

    //
    if (!config.shouldRetry (error)) {
      return Promise.reject (error);
    }

    // 0
    config.retryCount = config.retryCount || 0;

    // 
    if (config.retryCount >= config.retry) {
      return Promise.reject (error);
    }

    //
    config.retryCount += 1;

    //
    let backoff = new Promise (function (resolve) {
      setTimeout (function () {
        resolve ();
      }, config.retryDelay || 1);
    });

    //axios
    return backoff.then (function () {
      config.baseURL = ''; //TODO 404 app_gw  
      return axios (config);
    });
  }
  );
export default axios;



