import ExamBaseHeader from "./components/ExamBaseHeader";//
import RadarMap from "./components/RadarMap";/**/
import variables from "@/assets/js/variables.js";//
import {getSelAnsRes} from "@/assets/js/util/page-util.js";
import storage from "@/assets/js/util/storage.js";//localStorage sessionStorage 
export default{
  name:"HomeView",
  components: {
    ExamBaseHeader,
    RadarMap
  },
  data(){
    return {
     answerSelectMenu:variables.answerSelectType,//()
     examInfo:{
      exam_paper:{}
    },
    examId:"",//id
    paperId:"",//id
    topicList:[{img_info:{}}],//
    statisInfo:{},
    examUseTime:"00:00",//
    examStateClassArr:['no-qualified','qualified'],//clsss
    isShowTopicNum:false,//
    knowAnalList:[],//
    isknowAnalNoData:true,
  }
},
created () {//
  const self=this
  self.examId=self.$route.query.examId//id
  self.paperId=self.$route.query.paperId//id
  self.getPerformance()
  self.getPaperInfo()
},
destroyed(){

},
methods:{
     /**
     *  @Description: 
     *  @Author: ykl
     **/
     updateShowTuStatus(){
      const self=this
      self.isShowTopicNum=!self.isShowTopicNum
    },
     /** TODO:vue  
     *  @Description: 
     *  @Author: ykl
     */
     setNumOrderStatus(item){
      return getSelAnsRes(item).isCorrect
    },

     /**
     *  @Description: 
     *  @Author: ykl
     **/
     setExamUseTime(){
      const self = this
      if(!self.statisInfo.use_time)
        return;
      let timeDiff=Number(self.statisInfo.use_time)/1000
      let minutes = Math.floor(timeDiff/60)
      let seconds =parseInt((timeDiff%60).toFixed(0))
      self.examUseTime=self.updateTimerFormat(minutes)+":"+self.updateTimerFormat(seconds)
    },
     /**
     *  @Description: 0:0,00:00
     *  @Author: ykl
     **/
     updateTimerFormat(timeNum){
      return timeNum < 10 ? '0' + timeNum : '' + timeNum
    },
     /**
      *  @Description: 
      *  @Author: ykl
      **/
      getPerformance(){
        const self=this
        let url = "exam/performance/get_person_performance"
        let requestData = {
          plan_id: self.examId
        }
        self.$http.post(url, requestData).then(res => {
          self.setBaseData(res.data.instance)
        }).catch(err => {
        }) 
      },
      setBaseData(data){
        const self=this;
        self.examInfo=data.plan_info
        self.examInfo.exam_paper=data.paper_info

        if(!data.statistics_info)
          return
        self.statisInfo=data.statistics_info[0]
        self.setExamUseTime()

        let totalTopicNum=self.examInfo.exam_paper.question_num
        if(totalTopicNum==self.statisInfo.unanswered_num){//== 0
         self.statisInfo.errorTopicNum=0
         return
       }

       let trueAndNullNum=self.statisInfo.answer_right_num+self.statisInfo.unanswered_num
       if(totalTopicNum&&trueAndNullNum)
          self.statisInfo.errorTopicNum=totalTopicNum-trueAndNullNum//
      },
     /**
       *  @Description: 
       *  @Author: ykl
       **/
       getPaperInfo(){
        const self=this
        let url = "exam/performance/get_person_paper"
        let requestData = {
          plan_id: self.examId
        }
        self.$http.post(url, requestData).then(res => {
          self.topicList=res.data.instance.exam_record_questions
        }).catch(err => {
        }) 
      },
    /**
     *  @Description: 
     *  @Author: ykl
     *  @param index 
     **/
     toExamExplain(index=0){
       const self=this
       self.$router.push({
        path: '/exam-explain',
        query: {
         examId: self.examId,
         paperId:self.paperId,
         initTopicIndex: index
       }
     })
     }
   },
    filters:{
     /**
     *  @Description: ()
     *  @Author: ykl
     *  @param score 
     **/
     formatScore:function(score){
      if(typeof score!="number")
        return ""
      if(String(score).indexOf(".")==-1)
        return score
      return score.toFixed(1)
    }
  }
}



