 import BaseHeader from "@/components/common/BaseHeader";//
 import NoData from "@/components/common/NoData";//
 import variables from "@/assets/js/variables.js";//
 import {getSelAnsRes} from "@/assets/js/util/page-util.js";
 import storage from "@/assets/js/util/storage.js";//localStorage sessionStorage 
 export default{
   name:"ExamExplain",
   components: {
    BaseHeader,
    NoData
  },
  data(){
    return {
     examId:"",//id
     paperId:"",//id
     answerSelectMenu:variables.answerSelectType,//()
     answerResultMenu:variables.answerResultType,//()
     topicList:[{img_info:{}}],//
      currTopicIndex:0,//
      topicNum:0,//
      examInfo:{
       exam_paper:{}
     },
     statisInfo:{},
     answersOrderList:variables.answersOrderList,//
     baseImgUrl:variables.baseImgUrl,
     imgUrl:'',
     isShowImg:false,//
     isShowTnumList:false,//
     isTopicNoData:true,//
     imgs: [
     {
       url:'',
       title: ''
     }
     ]
   }
 },
 mounted () {
  const self=this
  let initTopicIndex=self.$route.query.initTopicIndex
  if(initTopicIndex)
    self.currTopicIndex=Number(initTopicIndex)
  self.examId=self.$route.query.examId//id
  self.paperId=self.$route.query.paperId//id
  self.getPerformance()
  self.getPaperInfo()
},
destroyed(){

},
watch:{
     /**
      *  @Description: 
      *  @Author: ykl
      *  @param newVal 
      *  @param oldVal 
      **/
      currTopicIndex:function(newVal,oldVal){
       const self=this
       self.updateShowImgStatus()
     }

   }, 
   methods:{
    /**
      *  @Description: 
      *  @Author: ykl
      */
      selectTopic(index){
        const self=this
        self.currTopicIndex=index
      },
     /** TODO:vue  
      *  @Description: 
      *  @Author: ykl
      */
      setNumOrderStatus(item){
       return getSelAnsRes(item).isCorrect
     },
      /**
      *  @Description: 
      *  @Author: ykl
      */
      lastTopic(){
        const self=this
        if(self.currTopicIndex>0)
         self.currTopicIndex--
     },
     /**
      *  @Description: 
      *  @Author: ykl
      */
      nextTopic(){
       const self=this
       if(self.currTopicIndex+1<self.topicNum)
         self.currTopicIndex++
     },
      /**
        *  @Description: 
        *  @Author: ykl
        **/
        getPerformance(){
         const self=this
         let url = "exam/performance/get_person_performance"
         let requestData = {
           plan_id: self.examId
         }
         self.$http.post(url, requestData).then(res => {
           self.setBaseData(res.data.instance)
         }).catch(err => {
         }) 
       },
       setBaseData(data){
         const self=this;
         self.examInfo=data.plan_info
         self.examInfo.exam_paper=data.paper_info
         let totalTopicNum=self.examInfo.exam_paper.question_num
         self.topicNum=totalTopicNum

         if(!data.statistics_info)
           return
         self.statisInfo=data.statistics_info[0]

         if(totalTopicNum==self.statisInfo.unanswered_num){//== 0
          self.statisInfo.errorTopicNum=0
          return
        }
        let trueAndNullNum=self.statisInfo.answer_right_num+self.statisInfo.unanswered_num
        if(totalTopicNum&&trueAndNullNum)
           self.statisInfo.errorTopicNum=totalTopicNum-trueAndNullNum//
       },
     /**
         /**
        *  @Description: 
        *  @Author: ykl
        **/
        getPaperInfo(){
         const self=this
         let url = "exam/performance/get_person_paper"
         let requestData = {
           plan_id: self.examId
         }
         self.$http.post(url, requestData).then(res => {
           let result=res.data.instance.exam_record_questions
           if(!result){
             self.isTopicNoData=true
             return;
           }
           for(let i=0;i<result.length;i++){
             let topicObj=result[i]
             let anSwerResult=getSelAnsRes(topicObj)//
             topicObj.selAnsNos=anSwerResult.selAnsNos//
             topicObj.correctNos=anSwerResult.correctNos//
             topicObj.isCorrect=anSwerResult.isCorrect//
           }
           self.topicList=result
           self.isTopicNoData=false
         }).catch(err => {
         }) 
       },
       /**
        *  @Description: 
        *  @Author: ykl
        **/
        updateShowTnumStatus(){
         const self=this
         self.isShowTnumList=!self.isShowTnumList
       },
       /**
        *  @Description: 
        *  @Author: ykl
        **/
        updateShowImgStatus(){
         const self=this
         let topicObj=self.topicList[self.currTopicIndex]
         if(!topicObj)
           return

         let imgInfo=topicObj.img_info
         if(imgInfo){
           self.isShowImg=true
           self.imgUrl=self.baseImgUrl+"/"+imgInfo.pic_path+"/"+imgInfo.pic_name
           self.imgs[0].url=self.imgUrl
         }else{
           self.isShowImg=false
         }
       }
     },
     filters:{
      /**
      *  @Description: ()
      *  @Author: ykl
      *  @param score 
      **/
      formatScore:function(score){
       if(typeof score!="number")
         return ""
       if(String(score).indexOf(".")==-1)
         return score
       return score.toFixed(1)
     }
   }
 }



