 import ExamBaseHeader from "./components/ExamBaseHeader";/**/
 import CircleProgress from "./components/CircleProgress";/**/
 import NoData from "@/components/common/NoData";//
 import variables from "@/assets/js/variables.js";//
 import storage from "@/assets/js/util/storage.js";
 import {formatDateTime} from "@/assets/js/util/page-util.js";
 import {ui} from "@/assets/js/util/page-ui.js";//
 export default{
   name:"HomeView",
   components: {
     ExamBaseHeader,
     CircleProgress,
     NoData,
   },
   data(){
     return {
       examStatusEnum:variables.examStatus,//
       personExamStatusEnum:variables.personExamStatus,//
       exerciseTypeEnum:variables.exerciseType,//
       page:1,
       pageSize:100,
       currExamList:[],//
       isCurrExamNoData:true,//
       histExamList:[],//
       isHistExamNoData:true,//
       timeFormatMnum:variables.dateType,
       questionBankList:[
         // knowledge_points:{}
       ],//
       scoreLevelImgArr:[
       "/static/images/oa-mobile/home/exam/test_mainicon_kao2.png",
       "/static/images/oa-mobile/home/exam/test_mainicon_kao1.png",
       "/static/images/oa-mobile/home/exam/test_mainicon_kao3.png"
       ],
       isQuestionBankNoData:true,//
       errorTopicNum:0,//
     }
   },
   mounted () {
     const self=this
     self.getCurrExamList()
     self.getHistExamList()
     self.getQuestionBankList()
     self.getErrorTopicNum()
   },
   destroyed(){

   },
   methods:{
     /**
      *  @Description: ()
      *  @Author: ykl
      */
      getCurrExamList(){
       const self=this
       let url = "exam/exam_staff/need_exam_plan_list"
       let requestData = {
         page: 1,
         page_size:100,
         exam_status:self.examStatusEnum.ISSUE.flag+","+self.examStatusEnum.EXAM_IN_EXECUTE.flag,
         person_exam_status:self.personExamStatusEnum.NO_START_EXAM.flag+","+self.personExamStatusEnum.EXAM_IN_EXECUTE.flag
       }
       self.$http.post(url, requestData).then(res => {
         let result=res.data.instance
         if(result.length>0){
           self.currExamList=result
           self.isCurrExamNoData=false
         }else{
           self.isCurrExamNoData=true
         }
       }).catch(err => {
       })
     },
       /**
      *  @Description: 
      *  @Author: ykl
      */
      getHistExamList(){
       const self=this
       let url = "exam/exam_staff/finish_plan_list"
       let requestData = {
         page: self.page,
         page_size:100,
         person_exam_status:self.personExamStatusEnum.HAND_IN_PAPER.flag
       }
       self.$http.post(url, requestData).then(res => {
        let result=res.data.instance
        if(result.length>0){
         self.histExamList=result
         self.setScoreLevel()
         self.isHistExamNoData=false
       }else{
         self.isHistExamNoData=true
       }
     }).catch(err => {
     })
   },
     /**
      *  @Description: 
      *  @Author: ykl
      */
      setScoreLevel(){
       const self=this
       self.histExamList.forEach((item)=>{
         let excellentScore=(item.plan_total_score * 0.8)
         if(item.actual_score<item.plan_passed_score){
           item.level=0
         }else if(item.actual_score>=item.plan_passed_score&&item.actual_score<=excellentScore){
           item.level=1
         }else if(item.actual_score>=excellentScore&&item.actual_score<=item.plan_total_score){
           item.level=2
         }
       })
     },
    /**
      *  @Description: 
      *  @Author: ykl
      */
      getQuestionBankList(){
       const self=this
       let url = "exam/question_depot/list_for_practice"
       let requestData = {
         page: self.page,
         page_size:100
       }
       self.$http.post(url, requestData).then(res => {
        let result=res.data.instance
        if(result.length>0){
         self.questionBankList=result
         self.isQuestionBankNoData=false
       }else{
         self.isQuestionBankNoData=true
       }
     }).catch(err => {
     })
   },
    /**
      *  @Description: 
      *  @Author: ykl
      */
      getErrorTopicNum(){
       const self=this
       let url = "exam/question_wrong/list"
       let requestData = {
         page: 1,
         page_size:1
       }
       self.$http.post(url, requestData).then(res => {
        let result=res.data
        if(result)
         self.errorTopicNum=result.total_size
     }).catch(err => {
     })
   },
     /**
      *  @Description: 
      *  @Parem: examId id
      *  @Author: ykl
      **/
      toExamNotice(examId){
        const self=this
        self.$router.push({
         path: '/exam-notice',
         query: {
           examId:examId
         }
       })
      },
     /**
      *  @Description: 
      *  @Parem: examId id
      *  @Parem: paperId Id
      *  @Author: ykl
      **/
      toExamAnswer(examId,paperId){
       const self=this
       if(!examId||!paperId)//id
         return 
       self.$router.push({
        path: '/exam-answer',
        query: {
         examId:examId,
         paperId:paperId
       }
     })
     },
      /**
      *  @Description: 
      *  @Parem: examId id
      *  @Parem: paperId Id
      *  @Author: ykl
      **/
      toExamOver(examId,paperId){
        const self=this
        self.$router.push({
         path: '/exam-over',
         query: {
           examId: examId,//id
           paperId:paperId//id
         }
       })
      },
        /**
      *  @Description: 
      *  @Parem: exerciseType 
      *  @Parem: topicBankId id  ""
      *  @Parem: topicNum   0
      *  @Author: ykl
      **/
      toExamExercise(exerciseType,topicBankId="",topicNum=0){
       const self=this
       if(self.errorTopicNum==0&&self.exerciseTypeEnum.ERROR_EXERCISE.flag==exerciseType){//0
         ui.msg("")
         return
       }
       if(topicNum==0&&self.exerciseTypeEnum.NORMAL_EXERCISE.flag==exerciseType){//0
         ui.msg("")
         return
       }

       self.$router.push({
        path: '/exam-exercise',
        query: {
         exerciseType:exerciseType,
         topicBankId:topicBankId
       }
     })
     }
   },
   filters: {
     formatDate: function (timestamp) {
      return formatDateTime(timestamp,variables.dateType.FONT_FORMAT.flag) 
    }
  }
}



