import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // id
   agentId: '1010000000000',
  //  
  agentName: ''
  },
  mutations: {
    // id
    change_agent_id (state, value) {
      state.agentId = value
    },
    // 
    change_agent_name (state, value) {
      state.agentName = value
    }
  }
})



