import BaseHeader from "@/components/common/BaseHeader";//
import NoData from "@/components/common/NoData";//
import storage from "@/assets/js/util/storage.js";//localStorage sessionStorage 
import variables from "@/assets/js/variables.js";//
import {formatDateTimem,getSelAnsRes} from "@/assets/js/util/page-util.js";
export default{
  name:"ExamAnswer",
  components: {
    BaseHeader,
    NoData
  },
  data(){
    return {
      headerTitle:"",//
      page:1,
      pageSize:1000,
      topicBankId:"",//id
      examInfo:{
        exam_paper:{}
      },//
      timeFormatMnum:variables.dateType,//
      answerSelectMenu:variables.answerSelectType,//()
      answerResultMenu:variables.answerResultType,//
      exerciseTypeEnum:variables.exerciseType,//
      exerciseType:0,//
      examStatusObj:{},//
      topicList:[{question:{}}],//  TODO:// question 
      currTopicIndex:0,//
      isFinishTopic:false,//
      isShowImg:false,//
      topicNum:0,//
      baseImgUrl:variables.baseImgUrl,
      imgUrl:'',
      isShowTnumList:false,//
      isShowTexp:false,//
      isTopicNoData:true,//
      isExistData:false,//
      answersOrderList:variables.answersOrderList,//
      alrAnswerIndexList:[],//
      currentAudio:"/static/audio/home/exam/error.mp3",
      imgs: [
      {
        url:'',
        title: ''
      }
      ]
      //currTopicObj:topicList[currTopicIndex]//
    }
  },
  created () {
    const self=this
    self.exerciseType = self.$route.query.exerciseType//
    let exerTypeArr=["",""]
    self.headerTitle=exerTypeArr[self.exerciseType]
    if(self.exerciseType==self.exerciseTypeEnum.ERROR_EXERCISE.flag){
      self.getErrorTopicList()
    }else if(self.exerciseType==self.exerciseTypeEnum.NORMAL_EXERCISE.flag){
      self.topicBankId=self.$route.query.topicBankId//id
      self.getNormalTopicList()
    }
  },
  watch:{
    /**
     *  @Description: 
     *  @Author: ykl
     *  @param newVal 
     *  @param oldVal 
     **/
     currTopicIndex:function(newVal,oldVal){
      const self=this
      self.updateShowTexpStatus(newVal)
      self.updateShowImgStatus()
    }

  }, 
  destroyed(){

  },
  methods:{
    /**
     *  @Description: 
     *  @Author: ykl
     */
     selectTopic(index){
       const self=this
       self.currTopicIndex=index
     },
    /** TODO:vue  
     *  @Description: 
     *  @Author: ykl
     */
     setNumOrderStatus(item){
      const self=this
      let answers=item.answers
      if(!answers)
        return

      if(self.isShowTexp){
        return getSelAnsRes(item).isCorrect
      }
      for(let i=0;i<answers.length;i++){
        if(answers[i].is_select==self.answerSelectMenu.SELECT.flag){
          return true
        }
      } 
    },
    /**
     *  @Description: 
     *  @Author: ykl
     */
     lastTopic(){
       const self=this
       if(self.currTopicIndex>0)
        self.currTopicIndex--
    },
    /**
     *  @Description: 
     *  @Author: ykl
     */
     nextTopic(){
      const self=this
      if(self.currTopicIndex+1<self.topicNum)
        self.currTopicIndex++
    },
    /**
     *  @Description: 
     *  @Author: ykl
     **/

     getErrorTopicList(){
      const self=this
      let url = "exam/question_wrong/list"
      let requestData = {
        page: self.page,
        page_size:self.pageSize  
      }
      self.$http.post(url, requestData).then(res => {
        let result=res.data.instance
        if(result.length==0){
          self.isTopicNoData=true
          return;
        }
        self.topicList=result
        self.topicNum=result.length
        self.isTopicNoData=false
      }).catch(err => {
      }) 
    },
     /**
     *  @Description: 
     *  @Author: ykl
     **/
     getNormalTopicList(){
      const self=this
      let url = "exam/question/list"
      let requestData = {
        page: self.page,
        page_size:self.pageSize,  //TODO ,
        depot_id:self.topicBankId
      }
      self.$http.post(url, requestData).then(res => {
        let result=res.data.instance
        if(result.length==0){
          self.isTopicNoData=true
          return;
        }
        self.topicNum=result.length
        for(let i=0;i<result.length;i++){//
          self.$set(self.topicList,i,{question:result[i]})//vue
        }
        self.isTopicNoData=false
      }).catch(err => {
      }) 
    },
    /**
     *  @Description: 
     *  @Parem: topicId id
     *  @Parem: topicBankId id       
     *  @Parem: answerIds id, 
     *  @Author: ykl    
     **/
     addErrorTopic(topicId,topicBankId,answerIds){
      const self=this
      let url = "exam/question_wrong/answered"
      let requestData = {
        question_id: topicId,
        depot_id:topicBankId, 
        answer_by_person:answerIds
      }
      self.$http.post(url, requestData).then(res => {
      }).catch(err => {
      }) 
    },
     /**
     *  @Description:
     *  @Author: ykl
     **/
     checkAnswer(){
      const self=this

      let topicObj=self.topicList[self.currTopicIndex].question
      if(!topicObj)
        return

      if(self.isShowTexp)
        return

      self.alrAnswerIndexList.push(self.currTopicIndex);//index
      let anSwerResult=getSelAnsRes(topicObj)//
      topicObj.selAnsNos=anSwerResult.selAnsNos//
      topicObj.correctNos=anSwerResult.correctNos//
      topicObj.isCorrect=anSwerResult.isCorrect//
      if(anSwerResult.isCorrect){//
        self.currentAudio="/static/audio/home/exam/correct.mp3"
      }else{//
        self.currentAudio="/static/audio/home/exam/error.mp3"
        if(anSwerResult.errSelAnsIdStr)
          self.addErrorTopic(topicObj.id,topicObj.depot_id,anSwerResult.errSelAnsIdStr)
      }
      setTimeout(()=>{
       self.$refs.hintAudio.play()
     },500)
      self.isShowTexp=true;//
    },
    /**
     *  @Description: 
     *  @Author: ykl
     **/
     updateShowImgStatus(){
      const self=this
      let topicObj=self.topicList[self.currTopicIndex].question
      if(!topicObj)
        return

      let imgInfo=topicObj.img_info
      if(imgInfo){
        self.isShowImg=true
        self.imgUrl=self.baseImgUrl+"/"+imgInfo.pic_path+"/"+imgInfo.pic_name
        self.imgs[0].url=self.imgUrl
      }else{
        self.isShowImg=false
      }
    },
    /**
     *  @Description: 
     *  @Author: ykl
     **/
     updateShowTexpStatus(index){
      const self=this
       for (let i = 0; i < self.alrAnswerIndexList.length; i++) {//
        let item=self.alrAnswerIndexList[i]
        console.log(item)
        if (index==item) {
          self.isShowTexp=true;//
          break;
        }else{
          self.isShowTexp=false;//
        }
      }
    },
    /**
     *  @Description: 
     *  @Author: ykl
     **/
     updateShowTnumStatus(){
      const self=this
      self.isShowTnumList=!self.isShowTnumList
    },
    /**
     *  @Description: 
     *  @Author: ykl
     **/
     selectAnswer(answerObj){
      const self=this
      if(self.isShowTexp)
        return
      
      if(answerObj.is_select==self.answerSelectMenu.NO_SELECT.flag||!answerObj.is_select)
        this.$set(answerObj, 'is_select', self.answerSelectMenu.SELECT.flag)
      else if(answerObj.is_select==self.answerSelectMenu.SELECT.flag)
        this.$set(answerObj, 'is_select', self.answerSelectMenu.NO_SELECT.flag)
    }
  }
}



