 import variables from  '@/assets/js/variables'


/**
 *  
 * @param timeStamp 
 * @param timeTypeFlag  dataTypeflag
 * @returns {*}
 * @Author: ykl
 */
 export const formatDateTime = (timeStamp, timeTypeFlag) => {
   let date;
   if (timeTypeFlag == variables.dateType.PRESENT_TIME_FORMAT.flag)
    date = new Date();
  else if (timeStamp)
    date = new Date(timeStamp);
  else
    return;

  let y = date.getFullYear();
  let m = date.getMonth() + 1;
  m = m < 10 ? ('0' + m) : m;
  let d = date.getDate();
  d = d < 10 ? ('0' + d) : d;
  let h = date.getHours();
  h = h < 10 ? ('0' + h) : h;
  let minute = date.getMinutes();
  let second = date.getSeconds();
  minute = minute < 10 ? ('0' + minute) : minute;
  second = second < 10 ? ('0' + second) : second;

  if (timeTypeFlag == variables.dateType.STRIKE_FORMAT.flag) {
    return y + '-' + m + '-' + d + ' ' + h + ':' + minute + ':' + second;
  } else if (timeTypeFlag == variables.dateType.SLASH_FORMAT.flag) {//
    return y + '/' + m + '/' + d + ' ' + h + ':' + minute + ':' + second;
  } else if (timeTypeFlag == variables.dateType.COLON_FORMAT.flag) {//
    return y + ':' + m + ':' + d + ' ' + h + ':' + minute + ':' + second;
  }else if(timeTypeFlag == variables.dateType.FONT_FORMAT.flag) {
    return y + '' + m + '' + d+ ' ' + h + ':' + minute + ':' + second;
  } else if(timeTypeFlag == variables.dateType.STRIKE_DATE_FORMAT.flag) {
    return y + '-' + m + '-' + d
  }
};

/**
 *  
 * @param topicObj ()
 */
 export const getSelAnsRes=(topicObj)=>{
  const result=new function(){
    this.correctAnswerNum = 0 //
    this.selectCorrectNum = 0 //
    this.correctNos=""//
    this.selAnsIdStr = ""//id 
    this.selAnsNos = ""//
    this.errSelAnsIdStr=""//id 
    this.isCorrect=false//
  }
  let answersOrderList=variables.answersOrderList//
  let answerResultMenu=variables.answerResultType//
  let answerSelectMenu=variables.answerSelectType//()
  let answers=topicObj.answers?topicObj.answers:[]

  
  for(let i=0;i<answers.length;i++){
    let item=answers[i]
    if(item.is_right==answerResultMenu.CORRECT.flag){
      result.correctAnswerNum++//
      result.correctNos+=answersOrderList[i]+" "
      if(item.is_select==answerSelectMenu.SELECT.flag)
        result.selectCorrectNum++//
    }
      //
      if(item.is_select==answerSelectMenu.SELECT.flag){
        result.selAnsIdStr+=item.id+","
        result.selAnsNos+=answersOrderList[i]+" "
        //
        if(item.is_right==answerResultMenu.ERROR.flag)
          result.errSelAnsIdStr+=item.id+","
      }
    }

    if(result.correctAnswerNum>0 && result.correctAnswerNum==result.selectCorrectNum && result.errSelAnsIdStr.length==0){//
      result.isCorrect=true
    }else if(result.errSelAnsIdStr.length>0 && result.correctAnswerNum>0){//
      result.errSelAnsIdStr = result.errSelAnsIdStr.substr(0, result.errSelAnsIdStr.length - 1);//id
      //self.addErrorTopic(topicObj.id,topicObj.depot_id,errSelAnsIdStr)
      result.isCorrect=false
    }
    return result
  }




